:root {
  --textColor: #009688;
  --textAugmentedColor: #047a6f; 
  --backgroundTextAlpha: #00968836;
  --background: #161c24;
  --background-hover: #0000001a;
  --background-hover-2: #0000003d;
  --normalText: #888888b6;
  --brighterText: #b9b9b9;
  --font-main: 'Space Mono', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  --font-sans: "Space Mono", "Rhodium Libre", sans-serif;
}

.App {
  text-align: center;
}

.tab-normal {
  color: #888888b6;
  color: var(--normalText);  
  font-size: 14px;
  padding: 4px 16px;
  margin: 8px 0px;
  font-weight: 700;
  border-radius: 4px;
  font-family: 'Space Mono', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-family: var(--font-main);
}

body {
  background-color: #161c24;
  background-color: var(--background);
}

.adv_title {
  font-family: "Space Mono", "Rhodium Libre", sans-serif;
  font-family: var(--font-sans);
  font-size: 14px;
  color: #888888b6;
  color: var(--normalText);
  font-weight: bold;
}

.tab-selected {
  background: #009688;
  background: var(--textColor);
  color: #343434;
}

.tab {
  cursor: pointer;
}

.tab:hover {
  background-color: #00968836;
  background-color: var(--backgroundTextAlpha);
}

.title_container{ 
  background-color: #00968836; 
  background-color: var(--backgroundTextAlpha);
  margin: 0px;
  padding: 0px;
  display: flex;
  /* border-bottom: 1px solid var(--normalText); */
  /* border-left: 1px solid var(--normalText); */
}

.title_image_string {
  height: 100%;
  width: 100%;
  margin: auto;
  object-fit: cover;
}

.color_container {

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #161920ad;
  z-index: 1000;
  border-radius: 4px;
}

.close_btn {
  position: absolute;
  border: none;
  background: #0000001a;
  background: var(--background-hover);
  font-family: 'Space Mono', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-family: var(--font-main);
  color: #888888b6;
  color: var(--normalText);
  font-weight: bold;
  font-size: 16px;
  right: 0px;
  padding: 12px 24px;
}

.close_btn:hover {
  background: #00968836;
  background: var(--backgroundTextAlpha);
  color: #b9b9b9;
  color: var(--brighterText);
}

.chrome_picker {
  position: absolute;
  margin: auto;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.graphy-title{
  font-family: "Righteous";
  color: #309c3fcd;
  font-weight: bold;
  align-self: middle;
  padding: 2px 12px;
  font-size: 24px;
}

.checkbox {
  padding: 8px;
  border: none;
  margin: 8px;
}

.title {
  text-transform: uppercase;
  font-family: "Space Mono", "Rhodium Libre", sans-serif;
  font-family: var(--font-sans);
  font-weight: bold;
  display: inline-block;
  font-size: 14px;
  padding: 4px 12px;
  color: #b9b9b9;
  color: var(--brighterText);
}

.simple_input {
  border: none;
  display: inline-block;
  font-family: 'Space Mono', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-family: var(--font-main);
  background-color: transparent;
  font-weight: 700;
  color: #888888b6;
  color: var(--normalText);
  font-size: 14px;
  flex-grow: 1;
  text-overflow: ellipsis;
}

ul {
  list-style-type: none;
  list-style-position: outside;
  margin: 0;
  padding: 0;
}

.input_container {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  /* border-bottom: 1px solid var(--normalText); */
}

.overflow-hidden {
  overflow-x: hidden;
  overflow-y: hidden;
}

.input_container:hover {
  background-color: #00968836;
  background-color: var(--backgroundTextAlpha);
}

.input_o_container {
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
}

.label{
  display: inline-block;
  font-family: 'Space Mono', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-family: var(--font-main);
  color: #009688;
  color: var(--textColor); 
  font-weight: bold;
  font-size: 12px;
  background-color: #0000001a;
  background-color: var(--background-hover);
  padding: 4px 12px;
}

.move_label {
  display: inline-block;
  color: #009688;
  color: var(--textColor);
  font-family: 'Space Mono', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-family: var(--font-main);
  display: flex;
  padding: 4px 0px;
  font-weight: bold;
  font-size: 14px;
}

.color_input {
  flex-grow: 1;
  width: 100%;
  display: flex;
  align-items: stretch;
  border: 1px solid #00968836;
  border: 1px solid var(--backgroundTextAlpha);
  padding: 0px;
  background-color: #00000000;
}

.simple-move-container {
  background-color: #0000003d;
  background-color: var(--background-hover-2);
  border: none;
  color: #888888b6;
  color: var(--normalText);
  font-family: 'Space Mono', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-family: var(--font-main);
  font-size: 14px;
  padding: 4px;
  border-radius: 4px;
  font-weight: bold;
}
.input_opt {
  background: #00968836;
  background: var(--backgroundTextAlpha);
  border-radius: 4px;
  border: none;
  font-size: 14px;
  padding: 4px 8px;
  color: #b9b9b9;
  color: var(--brighterText);
  font-weight: bold;
}

.simple-input-option {
  display: block;
  width: 100%;
  background: transparent;
  text-align: left;
  border: none;
  border-radius: 0px;
  color: #b9b9b9;
  color: var(--brighterText);
  padding: 4px 8px;
  font-family: 'Space Mono', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-family: var(--font-main);
  font-size: 12px;
  flex: 1 1;
}

.inp_select_option {
  background-color: #161c24;
  background-color: var(--background);
  color: #888888b6;
  color: var(--normalText);
}

.inp_select_option:hover {
  background-color: #00968836;
  background-color: var(--backgroundTextAlpha);
}

input:focus, textarea {
  outline: none !important;
}

.cfi {
  color: transparent;
}

.cfi::-webkit-file-upload-button {
  display: none;
}

.cfi::before {
  content: "Select Some files";
  display: inline-block;
  background-color: #0000003d;
  background-color: var(--background-hover-2);
  border: none;
  border-radius: 6px;
  padding: 6px 12px;
  color: #888888b6;
  color: var(--normalText);
  font-family: 'Space Mono', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-family: var(--font-main);
  font-weight: bold;
  font-size: 14px;
  cursor: pointer;
  -webkit-user-select: none;
}
.cfi:hover::before {
  color: #009688;
  color: var(--textColor);
  background-color: #00968836;
  background-color: var(--backgroundTextAlpha);
}

.file-name {
  color: #009688;
  color: var(--textColor);
  font-family: "Space Mono", "Rhodium Libre", sans-serif;
  font-family: var(--font-sans);
  border-radius: 4px;
  font-size: 12px;
  font-weight: bold;
}

table{
  table-layout: auto;
}

th, tr, td {
  border-left: 4px solid #161c24;
  border-left: 4px solid var(--background);
  border-top: 4px solid #161c24;
  border-top: 4px solid var(--background);
  border-radius: 12px;
}

.cell-data {
  padding: 4px 8px;
  font-family: 'Space Mono', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-family: var(--font-main);
  font-size: 12px;
  font-weight: bold;
  max-width: 128px;
  overflow: auto;
  background-color: #0000001a;
  background-color: var(--background-hover);
  color: #888888b6;
  color: var(--normalText);
  text-overflow: ellipsis;
  box-sizing: border-box;
}

.cell-data::-webkit-scrollbar {
  width: 0px;
  height: 1px;
}

.cell-index {
  text-align: center;
  font-family: 'Space Mono', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-family: var(--font-main);
  font-size: 14px;
  color: #009688;
  color: var(--textColor);
  font-weight: bold;
  text-align: left;
  padding: 4px 12px;
  background-color: #0000003d;
  background-color: var(--background-hover-2);
}

.table-container {
  padding: 8px;
  background: #1212120f;
  height: 75vh;
  margin: 4px;
  border-radius: 8px;
  overflow-y: scroll;
  overflow-x: scroll;
}

.table-container::-webkit-scrollbar {
  width: 4px;
}

.table-container::-webkit-scrollbar-thumb {
  background-color: #00968836;
  background-color: var(--backgroundTextAlpha);
  border-radius: 2px;
}

button:focus {outline:0;}

.table-container::-webkit-scrollbar-track {
  background-color: transparent;
}

.props-panel::-webkit-scrollbar {
  width: 4px;
}

.props-panel::-webkit-scrollbar-thumb {
  background-color: #7c4fb38f;
  border-radius: 2px;
}

.props-panel::-webkit-scrollbar-track {
  background-color: transparent;
}

.footer-container {
  background-color: #0000003d;
  background-color: var(--background-hover-2);
  padding-top: 24px;
  padding-bottom: 24px;
  text-align: center;
}

.footer-text {
  color: #888888b6;
  color: var(--normalText);
  font-weight: bold;
  display: inline-block;
  padding: 0px;
  margin: 0px;
  font-family: 'Space Mono', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-family: var(--font-main);
  font-size: 12px;
}

.link-icon {
  display: inline-block;
  -webkit-padding-start: 8px;
          padding-inline-start: 8px;
  font-size: 16px;
  color: #888888b6;
  color: var(--normalText);
  text-decoration: none;
  font-weight: bold;
}

.link-icon:hover{
  color: #009688;
  color: var(--textColor);
  text-decoration: none;
}

.data-art {
  font-weight: bold;
  font-size: 2rem;
  color: #00968836;
  color: var(--backgroundTextAlpha);
  font-family: 'Space Mono', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-family: var(--font-main);
  text-align: center;
}

.PointContainer {
  display: inline-block; 
}

@-webkit-keyframes Grow {
  from {top: 100%}
  to {top: 0%}
}

@keyframes Grow {
  from {top: 100%}
  to {top: 0%}
}

.button-play {
  border: none;
  border-radius: 100px;
  padding: 12px 16px 12px 18px;
  color:#009688;
  color:var(--textColor);
  font-size: 12px;
  background-color: #0000001a;
  background-color: var(--background-hover);
  cursor: pointer;
  position: relative;
}

.button-play:hover {
  color: #047a6f;
  color: var(--textAugmentedColor);
  background-color: #00968836;
  background-color: var(--backgroundTextAlpha);
}

.reset-btn {
  margin-left: 24px;
  font-size: 16px;
  padding: 4px 14px;
  border: none;
  text-transform: uppercase;
  background-color: transparent;
  font-family: 'Space Mono', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-family: var(--font-main);
  font-weight: bold;
  color: #009688;
  color: var(--textColor);
  letter-spacing: 1px;
}

.reset-btn:hover {
  color: #047a6f;
  color: var(--textAugmentedColor);
  background-color: #00968836;
  background-color: var(--backgroundTextAlpha);
  border-radius: 4px;
}

::selection {
  color: none;
  background: none;
}

.ins_header {
  font-family: #888888b6;
  font-family: var(--normalText);
  font-weight: bold; 
  color: #009688; 
  color: var(--textColor);
}

.ins-icon {
  padding: 8px 8px 8px 10px;
  border-radius: 14px;
  color: #009688;
  color: var(--textColor);
  font-size: 8px;
  background-color: #888888b6;
  background-color: var(--normalText);
}

.ins_container {
  background-color: #0000003d;
  background-color: var(--background-hover-2);
  padding: 12px 12px;
  border-radius: 8px;
  margin-top: 6px;
}

tr:hover, td:hover{
  color: #00000088;
  background-color: #00968836;
  background-color: var(--backgroundTextAlpha);
}

.ins-strong {
  color: #00000088;
  background-color: #888888b6;
  background-color: var(--normalText);
  padding: 0px 4px;
  border-radius: 4px;
}

.ins-table {
  border-radius: 2px;
}

.ins_r {
  border: none;
  border-radius: 0px;
}

.ins_d {
  border: 1px solid #888888b6;
  border: 1px solid var(--normalText);
  border-radius: 2px;
  font-size: 12px;
  background: #00968836;
  background: var(--backgroundTextAlpha);
  padding: 8px 16px;
}

.ins-container {
  overflow-x: hidden;
  font-family: 'Space Mono', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-family: var(--font-main);
  margin: auto;
  padding: 16px;
  display: inline-block;
  color: #888888b6;
  color: var(--normalText);
}

.ins_container h1 {
  font-size: 2em;
}

.ins_container h2 {
  font-size: 1.3em;
}
